p {
  padding: 0;
  margin: 0;
}

footer {
  background: #ada996;
  background: -webkit-linear-gradient(
    to right,
    #eaeaea,
    #dbdbdb,
    #f2f2f2,
    #ada996
  );
  background: linear-gradient(to right, #eaeaea, #dbdbdb, #f2f2f2, #ada996);

  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 1;
  transform: translateX(0);
  transition: transform 1s, opacity 1s;
}

footer.hiden-footer {
  opacity: 0;
  transform: translateX(-100%);
}

body.toggle-theme footer {
  background: #eb5757;
  background: -webkit-linear-gradient(to right, #eb5757, #000000);
  background: linear-gradient(to right, #eb5757, #000000);
}

.footer__team-link {
  color: #545454;
  padding: 0;
  margin: 0;
  text-decoration: underline;
}
@media screen and (min-width: 320px) {
  .copyright {
    margin: 0px;
    display: flex;
    justify-content: center;
    padding-top: 29px;
    padding-bottom: 10px;
  }

  .developers {
    margin: 0px;
    display: flex;
    justify-content: center;
    padding-bottom: 30px;
  }

  .page__footer {
    font-size: 14px;
    font-weight: 400;
  }
}

.footer-icon {
  margin-left: 10px;
  margin-right: 10px;
}

@media screen and (min-width: 768px) {
  .page__footer {
    display: flex;
    justify-content: center;
  }

  .copyright {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .developers {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .page__footer {
    font-size: 16px;
    font-weight: 400;
  }
}
