.modal-form-registration {
  position: fixed;
  overflow-y: scroll;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  padding: 30px;
  background: #333333;
  background: -webkit-linear-gradient(to right, #333333, #dd1818);
  background: linear-gradient(to right, #333333, #dd1818);
  display: block;
}

.modal-form-registration.is-hidden {
  display: none;
}

.title-wrapper {
  max-width: 500px;
  text-align: center;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  flex-direction: column;
  margin-bottom: 120px;
}

// .slider-wrapper {
//   position: absolute;
//   top: 10%;
//   left: 23%;
//   transform: perspective(600px) translate(0px, -100%) rotateY(45deg);
// }

.slider-tv {
  position: relative;
  max-width: 200px;
}

.icon-tv {
  position: absolute;
  fill: aliceblue;
  top: 0;
  left: 0;
}

.form-swiper {
  position: absolute;
  top: 80px;
  left: 12.5%;
}

.form-movies-img {
  border-radius: 10px;
  width: 160px;
  height: 100px;
}

.modal-form-registration_title {
  margin-bottom: 30px;
  color: var(--orange-color);
}

.modal-form-registration_sub-title {
  color: var(--orange-color);
}

.form-registration {
  position: relative;
  @include flexCenter();
  flex-direction: column;
  row-gap: 15px;
}

.man-watch {
  position: absolute;
  bottom: 39%;
  right: 28%;
  display: none;
}

.man-watch.hidden {
  display: block;
}

.icon-watch {
  fill: rgb(246, 244, 243);
}

.form-group-registaration {
  display: flex;
  flex-direction: column;
}

.email-form-title,
.password-form-title {
  display: flex;
  justify-content: start;
  margin-bottom: 5px;
  color: var(--orange-color);
}

.input-password,
.input-email,
.forgoten-password {
  width: 300px;
  height: 30px;
  padding: 10px;
  outline: 2px solid var(--gray-color);
  border: none;
  background-color: transparent;
  color: var(--light-color);
  border-radius: var(--border-radius);

  &:focus {
    outline: 2px solid var(--orange-color);
    transition: var(--animationTransition);
  }
}

.password-container {
  position: relative;
}

.icon-open {
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(-50%, -50%);
  opacity: 1;
  filter: invert(100%);
  -webkit-filter: invert(100%);
}

.icon-close {
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(-50%, -50%);
  opacity: 0;
  filter: invert(100%);
  -webkit-filter: invert(100%);
}

.icon-close.hide {
  opacity: 1;
}

.forgoten-group-registaration {
  display: none;
}

.forgoten-group-registaration.hidden {
  display: block;
}

.forfgot-password {
  color: var(--light-color);
  transition: color var(--animationTransition);

  &:hover,
  &:focus {
    color: var(--orange-color);
    outline: none;
  }
}

.forgotten-label {
  display: flex;
  margin-bottom: 5px;
  color: var(--orange-color);
}

.forgotten-container {
  display: flex;
}

.forgoten-password {
  margin-right: 15px;
}

.btn-new-password {
  @include flexCenter();
  position: relative;
  z-index: 1;
}

.form-group-registaration-btn {
  display: flex;
  flex-direction: row;
  column-gap: 15px;
}

.social-form-question {
  color: var(--light-color);
}

.button-sign-group {
  display: flex;
  column-gap: 15px;
}

@media (min-width: 320px) {
  .modal-form-registration_title {
    font-size: 18px;
    margin-bottom: 10px;
  }

  .modal-form-registration_sub-title {
    font-size: 14px;
  }

  .slider-wrapper {
    top: -29%;
    left: 50%;
    transform: perspective(600px) translate(0px, -100%) rotateY(0deg);
  }

  .slider-tv {
    max-width: 200px;
  }

  .icon-tv {
    position: absolute;
    fill: aliceblue;
    top: 0;
    left: 0;
    width: 150px;
    opacity: 0.4;
  }

  .form-swiper {
    position: absolute;
    top: 80px;
    left: 6%;
  }

  .form-movies-img {
    border-radius: 10px;
    width: 127px;
    height: 85px;
  }

  .man-watch.hidden {
    display: none;
  }

  .forgotten-label {
    font-size: 12px;
  }

  .forgoten-password {
    width: 250px;
  }
}

@media (min-width: 768px) {
  .slider-wrapper {
    left: 40%;
  }
}

@media (min-width: 1280px) {
  .modal-form-registration_title {
    font-size: 28px;
    margin-bottom: 10px;
  }

  .modal-form-registration_sub-title {
    font-size: 22px;
  }

  .slider-wrapper {
    position: absolute;
    top: 10%;
    left: 23%;
    transform: perspective(600px) translate(0px, -100%) rotateY(45deg);
  }

  .man-watch.hidden {
    display: block;
    opacity: 0.4;
  }
}
