:root {
  --border-radius: 5px;
  --light-color: #fff;
  --dark-color: #000;
  --gray-color: #545454;
  --modal-gray-color: #8c8c8c;
  --orange-color: #ff6b01;
  --ganre-orange-color: #ff6b08;
  --color-error: #ff001b;
  --vote-light-grey-color: #f7f7f7;
  --shadow: drop-shadow(0px 8px 43px rgba(255, 107, 1, .6));
  --animationTransition: 250ms cubic-bezier(.4, 0, .2, 1);
  --font-family: "Roboto", sans-serif;
  --font-weight-normal: 400;
  --font-weight-media: 500;
  --background-filter-header: rgba(0, 0, 0, .56);
}

html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

.lazyload, .lazyloading {
  opacity: 0;
  transition: all 1s;
  transform: translateX(-100%);
}

.lazyloaded {
  opacity: 1;
  transition: transform 1s, opacity 1s;
  transform: translateY(0);
}

.container {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
