  border: 0;
  border-radius: 5px;
  padding: 10px 20px;
}

.switch {
  width: 45px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch:focus {
  outline: none;
  transform: scale(1.1);
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: rgba(204, 204, 204, .3);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 25px;
  width: 25px;
  background: #fff url("https://i.ibb.co/7JfqXxB/sunny.png") center / 20px no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 15px rgba(32, 32, 32, .24);
}

input:checked + .slider {
  background-color: rgba(204, 204, 204, .3);
}

input:checked + .slider:before {
  background: #fff url("https://i.ibb.co/FxzBYR9/night.png") center / 16px no-repeat;
  transform: translateX(20px);
}

.slider.round {
  border-radius: 15px;
}

.slider.round:before {
  border-radius: 50%;
  box-shadow: 0 0 10px 5px rgba(247, 249, 249, .99);
}

body.toggle-theme .slider.round:before {
  box-shadow: 0 0 10px 7px rgba(238, 4, 4, .7);
}

